.work {
  padding: 120px 0 150px;
  background-color: #fff8e4;

  &__subtitle {
    margin: 0;

    font-weight: 600;
    font-size: 50px;
    line-height: 120%;
    text-align: center;
    color: #000;
  }

  &__content {
    padding: 0 20px;
  }

  &__list {
    width: 100%;
    column-gap: 123px;
    gap: 54px 123px;
    display: flex;
    text-align: center;
    justify-content: center;
    flex-wrap: wrap;
  }

  &__item {
    max-width: 330px;
  }

  &__name {
    margin: 0;
    padding: 23px 0;

    font-weight: 600;
    font-size: 25px;
    line-height: 140%;
    text-align: center;
    color: #000;
  }

  &__descr {
    margin: 0;

    font-weight: 400;
    font-size: 18px;
    line-height: 139%;
    text-align: center;
    color: #000;
  }
}
