.project {
  padding-top: 100px;
  padding-bottom: 150px;

  &__content {
    padding: 0 20px;
  }

  &__list {
  }

  &__item {
    padding-top: 250px;

    &:first-child {
      padding-top: 0;
    }
  }

  &__article {
    @include flex-all-sb;
    gap: 60px;
  }

  &__text {
    max-width: 500px;
  }

  &__subtitle {
    margin: 0;
    padding-bottom: 30px;

    font-weight: 600;
    font-size: 50px;
    line-height: 120%;
    color: #000;
  }

  &__info {
    margin: 0;
    padding-bottom: 30px;

    font-weight: 400;
    font-size: 20px;
    line-height: 150%;
    color: #000;
  }

  &__info-span {
    text-decoration: underline;
  }

  &__link {
    font-weight: 600;
    font-size: 20px;
    line-height: 150%;
    color: #000;
  }

  &__gif {
    width: 632px;
  }
}
