.team {
  padding-top: 120px;
  padding-bottom: 70px;
  &__container {
  }

  &__subtitle {
    margin: 0;
    padding-bottom: 25px;

    font-weight: 600;
    font-size: 50px;
    line-height: 120%;
    color: #000;
  }

  &__list {
    @include flex-all-sb;

    gap: 35px;
  }

  &__item {
    max-width: 390px;

    display: flex;
    flex-direction: column;
    align-self: stretch;
  }

  &__image {
  }

  &__name {
    margin: 0;

    font-weight: 600;
    font-size: 30px;
    line-height: 133%;
    color: #000;
  }

  &__info {
    margin: 0;
    padding-top: 34px;

    flex-grow: 1;

    font-weight: 400;
    font-size: 20px;
    line-height: 150%;
    color: #000;
  }

  &__link {
    padding-top: 25px;

    font-weight: 400;
    font-size: 20px;
    line-height: 150%;
    text-decoration: underline;
    color: #000;
  }
}
